import Star from './common/starComp/StarZoom'
export default {
  name: 'ProductOrder',
  components: {
    Star
  },
  props: {
    disabled: {
      type: Boolean,
      default () {
        return false
      }
    },
    checked: {
      type: Boolean,
      default () {
        return false
      }
    },
    info: {
      type: Object,
      default () {
        return {}
      }
    },
    companyInfo: {
      type: Object,
      default () {
        return {}
      }
    },
    categoryId: {
      type: String,
      default () {
        return ''
      }
    },
    categoryName: {
      type: String,
      default () {
        return ''
      }
    }
  },
  methods: {
    contact () {
      this.$emit('formShow', this.info.qcid)
    },
    navigateToDetail (info) {
      let routeUrl = this.$router.resolve({
        name: 'product-detail-id',
        params: { id: info.product_id }
      })
      window.open(routeUrl.href, '_blank')
    },
    onChange (e) {
      console.log(e.target)
      if (e.target.checked) {
        // 添加对比
        this.$emit('addCompare', e.target.id)
      } else {
        // 取消对比
        this.$emit('cancelCompare', e.target.id)
      }
    }
  }
}
